import React, { Component } from 'react';
import Swal from 'sweetalert2';

export default class FormComment extends Component {
    constructor(props) {
        super(props);

        this.state = {
            content: ''
        };
    }

    onChange = (event) => {
        let target = event.target;
        let name = target.name;
        let value = target.value;

        this.setState({
            [name]: value
        })
    }

    onSubmit = (event) => {
        event.preventDefault();

        if (this.props.account) {
            this.props.onSubmitComment(this.state.content);
            this.setState({content: ''});
        }
        else
            Swal.fire({
                icon: 'warning',
                title: 'Bn cha thc hin ng nhp',
            })
    }

    render() {
        let { content } = this.state;
        return (
            <div className="comment-form">
                <h4>Vit bnh lun ti y</h4>
                <form className="form-contact comment_form" id="commentForm" onSubmit={this.onSubmit} >
                    <div className="row">
                        <div className="col-12">
                            <div className="form-group">
                                <textarea className="form-control w-100" name="content" cols={30} rows={9}
                                    placeholder="Vit bnh lun..." style={{fontSize:'24px'}} value={content}
                                    onChange={this.onChange} required title="Nhp ni dung bnh lun"
                                />
                            </div>
                        </div>
                    </div>
                    <div className="form-group">
                        <button type="submit" className="button button-contactForm btn_1 boxed-btn">Gi bnh lun</button>
                    </div>
                </form>
            </div>
        );
    }
}