import React, { Component } from 'react';
import callAPI from '../../../utils/connectAPI';
import { MDBDataTable, Row, Col, Card, CardBody } from 'mdbreact';
import Swal from 'sweetalert2';
import ModalActionsProduct from './ModalActionsProduct';

class DataTable extends Component {
    constructor(props) {
        super(props);
        this.state = {
            data: {
                columns: [

                ],
                rows: [
                   
                ]
            },
            product: null,
            statusBtnAdd: true
        };
    }

    onSetStateData = (products) => {
        this.setState({
            data : {
              columns: [
                {
                    label: 'Tn sn phm',
                    field: 'name',
                    sort: 'asc',     
                },
                {
                    label: 'Loi sn phm',
                    field: 'productType',
                    sort: 'asc',
                },
                {
                    label: 'M t',
                    field: 'description',
                    sort: 'asc',
                },
                {
                    label: 'Hnh nh',
                    field: 'images' ,
                    sort: 'asc',
                },
                {
                    label: 'Gi',
                    field: 'price',
                    sort: 'asc',
                },
                {
                    label: 'S lng',
                    field: 'quantity',
                    sort: 'asc',
                },  
                {
                    label: 'Cp nht',
                    field: 'update',      
                },
                {
                    label: 'Xa',
                    field: 'delete'
                }  
              ],
                rows: products.map((row, key) => ({
                    ...row,
                    images: (
                        <img src={row.images} alt="" style={{width: '50px'}}  />
                    ),
                    update: (
                        <button className="btn btn-warning" data-toggle="modal" data-target="#toggleUpdate" onClick={() => this.onOpenToggleModal(row)}>
                            <i className="fa fa-edit"></i>
                        </button>
                    ),
                    delete: (
                        <button className="btn btn-danger">
                            <i className="fa fa-trash" onClick={() => this.deleteProduct(row._id)}></i>
                        </button>
                    )
                }))
            }
        });
    }

    onOpenToggleModal = (product) => {
        // if product != null call modal edit
        if (product)
            this.setState({product})
        else
            this.setState({product: {}})
    }
    
    deleteProduct = (id) => {
        Swal.fire({
            title: 'Bn c chc?',
            text: "Bn mun xa sn phm ny!",
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'C',
            cancelButtonText: 'Khng'
        }).then((result) => {
            if (result.value) {
                var tempData = this.state.data.rows.filter(item => {
                    item.images = item.images.props.src;
                    return item._id !== id;
                });
                this.onSetStateData(tempData);
        
                callAPI(`admin/product/`+id, 'DELETE', null)
                    .then(res => {
                        Swal.fire({
                            icon: 'success',
                            title: res.data.messSuccess,
                        });
                    })
                    .catch((err) => { console.log(err) })
            }
        })
    }

    componentDidMount() {
        callAPI('admin/product', 'GET', null)
            .then(res => {
                this.onSetStateData(res.data);
            })
            .catch((err) => { console.log(err) })
    }

    updateInfoProduct = (product) => {
        let products = this.state.data.rows;
                          
        if (product._id) {
            callAPI(`admin/product/update/${product._id}`, 'POST', product)
                .then(res => {
                    if (res.data.messSuccess) {
                        Swal.fire({
                            icon: 'success',
                            title: res.data.messSuccess,
                        });

                        // console.log(product);
                        products = products.map(item => {
                            item.images = item.images.props.src;  // v sao khi update th field images tr thnh 1 object nn phi chuyn n thnh String
                            if (item._id === product._id)
                                item = product;
                            return item;
                        })
                        this.onSetStateData(products);
                        // console.log("PRODUCT UPDATED");
                    }
                }).catch(err => console.log(err));
        }
        else {
            callAPI('admin/product/add', 'POST', product)
                .then(res => {
                    if (res.data.result) {
                        Swal.fire({
                            icon: 'success',
                            title: 'Sn phm  c thm thnh cng',
                        });

                        // console.log(product);
                        products = products.map(item => {
                            item.images = item.images.props.src;  // v sao khi update th field images tr thnh 1 object nn phi chuyn n thnh String
                            if (item._id === product._id)
                                item = product;
                            return item;
                        })
                        products.unshift(res.data.result);
                        this.onSetStateData(products);
                    }
                })
                .catch(err => console.log(err));
        }
    }

    render() {
        var { data, product } = this.state;
        return (
           
            <Row className="mb-4">
                
                <Col md="12">
                    
                <button className="btn btn-primary add-modal-product" data-toggle="modal" data-target="#toggleUpdate" onClick={() => this.onOpenToggleModal(null)}>
                    <i className="fa fa-plus"> Thm sn phm</i>
                </button>
                
                    <Card>

                        <CardBody>
                        
                            <MDBDataTable
                                striped
                                bordered
                                hover
                                data={data}
                                entriesLabel="Hin th"
                                infoLabel={["Hin th", "n", "trong s", "mc"]}
                                pagesAmount={10}
                                paginationLabel={["Trang trc", "Trang k"]}
                                searchLabel="Tm theo tn sn phm"
                                noBottomColumns
                            >
                            </MDBDataTable>
                            
                        </CardBody>

                    </Card>

                </Col>
                { product ? <ModalActionsProduct product={product} updateInfoProduct={this.updateInfoProduct} /> : null }
            </Row>

        );
    }
}

export default DataTable;