import React, { Component } from 'react';
import Swal from 'sweetalert2';

import StarRating from '../../components/Evaluation/StarRating';

import './Product.css'

class ProductDetail extends Component {
    onChoose = (product, quantity) => {
        let tempQuantity = product.quantity;
        let cart = JSON.parse(sessionStorage.getItem("cart"));
        if (cart == null)
            cart = {
                products: [],
                total: 0
            };
        let countCart = 0;
        countCart = Number(sessionStorage.getItem("countCart"));
        if (countCart >= 10)
            Swal.fire({
                icon: 'warning',
                title: "Cnh bo",
                text: "S lng sn phm trong gi hng khng c vt qu 10",
            })
        else {
            let { products } = cart;
            let tempProduct = product;
            if (products.length > 0) {
                let flag = false;   // this product hasn't in cart
                cart.total = 0;     // reset total = 0 in cart
                products.forEach(el => {
                    if (el._id === product._id) {
                        flag = true;
                        el.quantity += quantity;

                        // kim tra s lng sn phm ny trong kho c cn cung cp  hay ko?
                        if (el.quantity > product.quantity) {
                            el.quantity -= quantity;    // tr li y c s lng sn phm ny trong gi hng
                            countCart -= quantity;
                            Swal.fire({
                                icon: 'warning',
                                title: "Cnh bo",
                                text: "S lng sn phm trong kho khng   thm vo gi hng na",
                            })
                        }
                    }
                    cart.total += el.quantity * el.price;
                });
                if (!flag) {
                    tempProduct.quantity = quantity;
                    cart.total += tempProduct.price * quantity;
                    products.push(tempProduct);
                }
            }
            else {
                tempProduct.quantity = quantity;
                cart.total = tempProduct.price * quantity;
                products.push(tempProduct);
            }
            countCart += quantity;
            // cart.products = products;

            sessionStorage.setItem("cart", JSON.stringify(cart));
            sessionStorage.setItem("countCart", Number(countCart));

            // refresh
            let amount = this.props.amountCurrentItemCart;
            this.props.onAddItemIntoCart(++amount, product, tempQuantity);
        }
    }

    render() {
        let { product } = this.props;
        return (
            <div className="blog_area single-post-area section-padding">
                <div className="container">
                    <div className="row">
                        <div className="col-lg-6 col-md-12 col-sm-12" >
                            <img src={product.images} alt="image_product" style={{ width: '500px', height: '500px' }}/>
                        </div>
                        <div className="col-lg-6 col-md-12 col-sm-12">
                            <h1 className="display-5 text-left">{product.name}</h1>
                            {
                                product.status === true ?
                                    <p className="text-bold">Trng thi: <span className="text-green">Cn hng</span></p>
                                    :
                                    <p className="text-bold">Trng thi: <span className="text-red">Ht hng</span></p>
                            }
                            <hr />
                            <p className="text-bold">Loi sn phm: {product.productType}</p>
                            <p className="text-bold">Gi: ${product.price}</p>
                            <div className="rating_days d-flex justify-content">
                                <StarRating
                                    numberOfStars={5}
                                    value={Math.round(product.rating)}
                                    size={15}
                                    editing={false}
                                /> &nbsp;<span>({product.review} lt nh gi)</span>
                            </div>
                            { product.status === true ?
                                    <button className="btn btn-warning" onClick={() => this.onChoose(product, 1)}><i className="fa fa-shopping-cart" /> Chn mua</button>
                                    : null
                            }
                        </div>
                    </div>
                    <hr />
                    <div className="row">
                        <div className="col-12" >
                            <h1>M T SN PHM</h1>
                            <p>{product.description}</p>
                        </div>
                    </div>
                </div>
            </div>


        );
    }
}

export default ProductDetail; 