import React, { Component } from 'react';
import Moment from 'react-moment';
import Swal from 'sweetalert2';

import { MDBRow, MDBCol, MDBCardBody, MDBMask, MDBIcon, MDBView } from "mdbreact";

// import icons
import iconOnline from '../../Post/Icons/iconOnline.png';
import iconWaiting from '../../Post/Icons/iconWaiting.png';
import iconGray from '../../Post/Icons/iconGray.png';

export default class PostUser extends Component {

    showStatusOfPost = (status) => {
        let strStatus = "", icon = null;
        switch (status) {
            case 'A':
                strStatus = " duyt";
                icon = iconOnline;
                break;
            case 'D':
                strStatus = " xa";
                icon = iconGray;
                break;
            case 'W': default:
                strStatus = "ang duyt";
                icon = iconWaiting;
                break;
        }
        return (
            <label>
                {strStatus} &nbsp;&nbsp; <img src={icon} alt='icon' />
            </label>
        )
    }

    onHandlingPost = (acctions) => {
        let text = '';
        if (acctions === 1)
            text = "Bn mun duyt bi ng ny!";
        else if (acctions === 2)
            text = "Bn mun xa bi ng ny!"
        Swal.fire({
            title: 'Bn c chc?',
            text,
            icon: 'warning',
            showCancelButton: true,
            confirmButtonColor: '#3085d6',
            cancelButtonColor: '#d33',
            confirmButtonText: 'Yes',
            cancelButtonText: 'No'
        }).then((result) => {
            if (result.value) this.props.onHandlingPostOfUser(this.props.currentPost, acctions);
        })
    }

    render() {
        let { currentPost } = this.props;
        return (
            <MDBCol lg="12" xl="12">
                <div className="d-flex">
                    <div style={{ float: "left", width: "95%", textAlign: "left" }}>
                        <p>
                            ng bi: &nbsp;&nbsp; <strong> {currentPost.account.person.name} </strong>
                        </p>
                    </div>
                    <div style={{ float: "right", width: "50%", textAlign: "left" }}>
                        <p>
                            Trang thi: &nbsp; {this.showStatusOfPost(currentPost.status)}
                        </p>
                    </div>
                </div>
                <p className="d-flex">
                    Ngy ng: &nbsp;&nbsp;
                    <Moment format="YYYY-MM-DD HH:mm:ss">
                        {currentPost.postDate}
                    </Moment>
                </p>
                <p className="d-flex">
                    {currentPost.content}
                </p>
                <MDBRow>
                    {currentPost.images.map((img, index) => {
                        if (currentPost.images.length === 1) {
                            return (
                                <MDBCol key={index} lg="12" md="12" className="mb-lg-0 mb-4">
                                    <MDBView hover className="rounded z-depth-2 mb-4" waves>

                                        <img
                                            className="img-fluid"
                                            src={img}
                                            alt=""
                                            width="100%" />

                                        <MDBMask overlay="white-slight" />
                                    </MDBView>
                                </MDBCol>
                            )
                        } else if (currentPost.images.length === 2) {
                            return (
                                <MDBCol key={index} lg="6" md="12" className="mb-lg-0 mb-4">
                                    <MDBView hover className="rounded z-depth-2 mb-4" waves>

                                        <img
                                            className="img-fluid"
                                            src={img}
                                            alt=""
                                            width="100%" />

                                        <MDBMask overlay="white-slight" />
                                    </MDBView>
                                </MDBCol>
                            )
                        } else {
                            return (
                                <MDBCol key={index} lg="4" md="12" className="mb-lg-0 mb-4">
                                    <MDBView hover className="rounded z-depth-2 mb-4" waves>

                                        <img
                                            className="img-fluid"
                                            src={img}
                                            alt=""
                                            width="100%" />

                                        <MDBMask overlay="white-slight" />
                                    </MDBView>
                                </MDBCol>
                            )
                        }
                    })}
                </MDBRow>
                <MDBCardBody>
                    <span className="mr-2">
                        <button onClick={() => this.addLike(currentPost._id)}><MDBIcon icon="heart" /></button> {currentPost.sumLike} thch
                    </span>

                    <span>
                        <MDBIcon icon="comment" /> {currentPost.comments.length} bnh lun
                    </span>
                </MDBCardBody>
                <button className="btn btn-info" style={{marginRight: "15px"}} onClick={() => this.onHandlingPost(1)} > Duyt bi </button>
                <button className="btn btn-danger" onClick={() => this.onHandlingPost(2)}> &nbsp;Xa bi&nbsp; </button>
                <hr className="my-5" />
            </MDBCol>
        );
    }
}